export default {
  created() {
    try { require(`~/assets/img/icon/${this.name}.svg?sprite`).default }
    catch (err) {}
  },
  props: {
    name: {
      type: String,
      required: true
    },
    height: {
      type: String,
    },
    width: {
      type: String,
    },
    fill: {
      type: String,
    },
    color: {
      type: String,
    },
  },
  data: () => ({
  }),
}   
