import Vue from 'vue'
import { gsap } from 'gsap'
import { TextPlugin } from 'gsap/TextPlugin'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

if (process.client) {
    gsap.registerPlugin(TextPlugin, ScrollTrigger)
}

Object.defineProperty(Vue.prototype, '$gsap', { value: gsap })

